/* master */
body {
  font-family: 'Rubik', sans-serif;
  background: #FFFFEA;
  background-size: 20px 20px;
  background-image: linear-gradient(to right, #e9f0f0 1px, transparent 1px), linear-gradient(to bottom, #e9f0f0 1px, transparent 1px); }

/* main menu */
.menuButton,
.article__button {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 4rem;
  height: 4rem;
  right: 0;
  bottom: 0;
  padding: 0;
  margin: 2rem;
  border: 3px solid #1a1a1a;
  background: #fefefe;
  transform: rotate(90deg);
  z-index: 2; }

.article__button.article__button-position {
  left: 0;
  transform: rotate(0deg); }

.menuButton:hover {
  cursor: pointer; }

.menuButton__line {
  font-size: 3rem;
  color: #1a1a1a;
  transform: rotate(45deg); }

.switch__line-off {
  animation-name: line-off;
  animation-duration: .6s;
  animation-fill-mode: both; }

.switch__line-on {
  animation-name: line-on;
  animation-duration: .6s;
  animation-fill-mode: both; }

.menuButton__switch-off {
  animation-name: button;
  animation-duration: .6s;
  animation-fill-mode: both; }

.menuButton__switch-on {
  animation-name: button-on;
  animation-duration: .6s;
  animation-fill-mode: both; }

@keyframes line-off {
  from {
    transform: rotate(135deg);
    color: #efefef; }
  to {
    transform: rotate(45deg);
    color: #1a1a1a; } }

@keyframes line-on {
  from {
    transform: rotate(45deg);
    color: #1a1a1a; }
  to {
    transform: rotate(135deg);
    color: #fff; } }

@keyframes button {
  from {
    background-color: #1a1a1a;
    border-color: #fefefe; }
  to {
    background-color: #fefefe;
    border-color: #1a1a1a; } }

@keyframes button-on {
  from {
    background-color: #fefefe;
    border-color: #1a1a1a; }
  to {
    background-color: #1a1a1a;
    border-color: #fefefe; } }

.menu {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #8B7871;
  transform: translateX(-100%);
  padding: 2rem;
  z-index: 1; }

.menu-on {
  animation-name: menu-on;
  animation-duration: .6s;
  animation-fill-mode: both; }

.menu-off {
  animation-name: menu-off;
  animation-duration: .6s;
  animation-fill-mode: both; }

@keyframes menu-on {
  from {
    transform: translateX(-100%); }
  to {
    transform: translateX(0); } }

@keyframes menu-off {
  from {
    transform: translateX(0); }
  to {
    transform: translateX(-100%); } }

.menu__section {
  display: none; }

.menu__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 8px; }

.menu__item {
  margin-top: 1rem;
  list-style: none; }

.menu__link {
  font-size: 1.5rem;
  color: #FFFFEA;
  text-decoration: none;
  transition: color .2s linear; }

.menu__link:hover {
  color: rgba(255, 255, 234, 0.7); }

.menu__description {
  display: none; }

.strong__header {
  text-transform: uppercase; }

/* hero */
.hero {
  padding: 8px;
  text-align: center; }

.hero__header {
  font-size: 2rem;
  padding: 40px;
  text-align: center;
  animation: 200ms 500ms linear both heroHeader;
  margin: 0; }

.hero__header::after {
  content: " </>";
  display: flex;
  justify-content: center;
  margin-top: 8px;
  transform: rotate(15deg);
  background: -webkit-linear-gradient(#eee, #333);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; }

.article__nav {
  position: sticky;
  top: 0;
  left: 0; }

.article__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 0;
  margin-top: 0;
  padding: 8px;
  list-style: none;
  background: #ffa07a;
  overflow: hidden;
  transform: scaleY(0);
  transform-origin: top;
  transition: transform 0.3s ease-out; }

.article__list-on {
  height: auto;
  transform: scaleY(1); }

.article__list-off {
  animation-name: articleList-off;
  animation-duration: .6s;
  animation-fill-mode: both; }

@keyframes articleList-on {
  from {
    height: 0;
    opacity: 0; }
  to {
    height: auto;
    opacity: 1; } }

@keyframes articleList-off {
  from {
    height: auto;
    opacity: 1; }
  to {
    height: 0;
    opacity: 0; } }

.article__item {
  width: 100%; }

.article__link {
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  color: black;
  padding: 8px;
  text-align: center;
  transition: background-color .2s linear; }

.article__link:hover {
  background-color: rgba(0, 0, 0, 0.15); }

.article__header {
  padding: 8px;
  text-align: center; }

.article__paragraph,
.section {
  padding: 16px; }

.article__paragraph {
  text-align: center; }

.section__code,
.card__code {
  display: block;
  background: #C0D3D3;
  margin-top: 8px;
  margin-bottom: 8px;
  padding: 16px;
  font-size: 0.8rem; }

.card__link,
.section__link {
  display: block;
  margin: 8px; }

.card {
  padding: 8px;
  margin: 4px;
  border: 1px solid black;
  border-radius: 4px; }

.card__header {
  color: #4ECC6F; }

.image {
  display: block;
  width: 100%;
  margin: 8px auto;
  padding: 8px; }

.wrong {
  color: red; }

.table {
  width: 100%;
  border: 1px solid black;
  margin: 20px auto;
  text-align: center;
  border-collapse: collapse; }

.table__caption {
  text-align: center;
  border: 1px solid black;
  background: #BFBFB0;
  padding: 10px; }

.table__cell {
  padding: 10px; }

.table__cell:nth-child(1) {
  border-right: 1px solid black; }

.table__head .table__cell {
  border: 1px solid black; }

.table__cell.table__cell--truthy {
  background: #B2CCA7; }

.table__cell.table__cell--falsy {
  background: #A7A9CC; }

@keyframes heroHeader {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

/* footer */
.footer {
  padding: 24px;
  text-align: center;
  font-size: 1rem;
  background: #8B7871; }

.footer__paragraph {
  margin: 0 80px; }

@media (min-width: 640px) and (orientation: landscape) {
  .menu__list {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between; }
  .menu__item {
    margin: 1rem; }
  .menu__link {
    font-size: 1rem; } }

@media (min-width: 768px) and (orientation: portrait) {
  body {
    font-size: 1.2rem; } }

@media (min-width: 1024px) {
  .menu {
    align-items: center; }
  .menu__section {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 600px; }
  .hero__header {
    font-size: 3rem; } }

@media (min-width: 768px) and (orientation: portrait) {
  body,
  .footer {
    font-size: 1.6rem; } }

@media (min-width: 850px) and (orientation: landscape) {
  .menu__list {
    margin: 0; } }

@media (min-width: 1366px) {
  body {
    font-size: 1.4rem; }
  .hero {
    background: #4ecc6f;
    background: -moz-radial-gradient(circle, #4ecc6f 0%, rgba(255, 255, 255, 0) 62%);
    background: -webkit-radial-gradient(circle, #4ecc6f 0%, rgba(255, 255, 255, 0) 62%);
    background: radial-gradient(circle, #4ecc6f 0%, rgba(255, 255, 255, 0) 60%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#4ecc6f", endColorstr="#ffffff", GradientType=1); }
  .hero__header {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
    padding: 40px;
    text-align: center;
    animation: 200ms 500ms linear both heroHeader;
    margin: 0; }
  .hero__header::after {
    margin: 20px; }
  .article__button {
    display: none; }
  .wrapper {
    max-width: 1100px;
    width: 100%;
    margin: 0 auto; }
  .article__menu {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap; }
  .article__list {
    width: 100%;
    max-width: 1100px; }
  .menu__section {
    font-size: 1.1rem; }
  .menu__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 8px; }
  .menu__description,
  .article__list {
    display: flex;
    flex-direction: row;
    height: auto;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 10px;
    transform: none; }
  .menu__description {
    flex-direction: column;
    align-items: flex-start;
    line-height: 1.4rem; }
  .menu__item {
    margin: 0; }
  .menu__link,
  .article__link {
    display: block;
    text-decoration: none;
    text-transform: uppercase;
    color: black;
    padding: 10px;
    transition: background-color .2s linear; }
  .menu__link {
    display: flex;
    flex-direction: column;
    width: 250px;
    height: 200px;
    border: 1px solid black;
    text-transform: none; }
  .menu__link:hover,
  .article__link:hover {
    color: #1a1a1a;
    background-color: rgba(0, 0, 0, 0.15); }
  .article__list::after {
    content: "\25BA";
    display: block;
    font-size: 2rem;
    margin-left: 30px;
    transform: rotate(180deg);
    animation: heroHeader 1s linear 1.5s infinite alternate both; }
  .article {
    font-size: 1.2rem; }
  .article__item {
    width: auto; }
  .menu__text,
  .article__paragraph {
    max-width: 800px;
    width: 100%;
    margin: 20px auto;
    text-align: center; }
  .menu__header,
  .article__header {
    text-align: center;
    margin: 20px 20px; }
  .section {
    padding-top: 86px;
    margin-bottom: 40px; }
  .section__header {
    margin: 10px 0; }
  .section__paragraph,
  .card__paragraph {
    margin: 10px 0; }
  .section__list,
  .card__list {
    margin: 10px 10px 10px 30px; }
  .section__code,
  .card__code {
    display: inline-block;
    font-size: 1rem; }
  .card {
    padding: 10px;
    margin: 5px;
    border: 1px solid black;
    border-radius: 5px; }
  .card__header {
    color: #4ECC6F; }
  .image {
    display: block;
    width: auto;
    max-width: 1100px;
    margin: 10px auto;
    padding: 10px; }
  .section__paragraph.section__paragraph-position {
    text-align: center; }
  .footer {
    font-size: 2rem; } }
